import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';

import { App } from 'components/App';
import './index.css';
import './baseCSS/modern-normalize.css';
import './baseCSS/project-normalize.css';

//   Redux     Provider:
import { Provider } from 'react-redux';
// Provider  store,     :
import storeRedux, { persister } from './redux/indexStore';
import { PreLoader } from 'components/Preloader/PreLoader';
import { PersistGate } from 'redux-persist/integration/react';

//    -  -  useSelector
//      -  useDispatch

ReactDOM.createRoot(document.getElementById('root')).render(
  <React.StrictMode>
    {/*  App  Provider,   App    store */}
    <Provider store={storeRedux}>
      {/*  App  PersistGate    localStorage */}
      <PersistGate loading={<PreLoader />} persistor={persister}>
        {/* basename="/goit-react-hw-05-movies" -     */}
        <BrowserRouter basename="goit-react-hw-08-phonebook">
          <App />
        </BrowserRouter>
      </PersistGate>
    </Provider>
  </React.StrictMode>
);

